.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
  animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #08ac08;
  height: auto;
  padding: 20px;
  color: white;
  text-transform: uppercase;
  text-shadow: 2px 2px 2px black; }

.App-title {
  font-size: 20px; }
  @media (min-width: 482px) {
    .App-title {
      font-size: 40px; } }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

.player_container {
  padding: 20px;
  text-align: left; }
  @media (min-width: 480px) {
    .player_container {
      padding: 40px; } }

.player {
  margin: 30px; }

.player_row {
  margin: 30px auto;
  display: block; }
  @media (min-width: 480px) {
    .player_row {
      display: -ms-flexbox;
      display: flex;
      -ms-flex: 1 1;
      flex: 1 1;
      -ms-flex-align: center;
      align-items: center; } }
  .player_row p {
    padding: 0 20px; }
  .player_row h2 {
    display: block;
    padding: 10px 0; }
    @media (min-width: 480px) {
      .player_row h2 {
        display: inline;
        padding: 20px 10px; } }
  .player_row div {
    border: 1px solid black;
    border-right: 0; }
    .player_row div:last-of-type {
      border-right: 1px solid black; }

.player-icon {
  width: auto;
  height: 95px;
  border-radius: 0; }

.player_score {
  padding: 20px; }
  .player_score p {
    display: inline;
    padding: 20px 10px; }



